export default {
	name: 'UniCarousel',
	props: {
		Arr: {
			type: Array,
			default: []
		},
		ArrS: {
			type: Array,
			default: []
		},
		ArrT: {
			type: Array,
			default: []
		},
		ArrF: {
			type: Array,
			default: []
		},
		index: {
			type: Number,
			default: 0
		},
		isaniok: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			ArrCopy: []
		};
	},
	created() {
		this.ArrCopy = this.Arr.slice(1);
		this.ArrCopy.push(this.Arr[0]);
